<template>
  <div class="to-clock daka-img-box flex-column">
    <img src="../../imgs/text-noclock.png" class="to-tip">
    <template>
      <div v-if="!isSupply"
         class="btn-box flex"
         :class="{'disable': disable}"
         @click="toPinchin">
        <span class="btn-text">去打卡</span>
      </div>
      <template v-else>
        <div v-if="!supplyDisable"
          class="btn-box flex"
          @click="toSignPinchin">
          <span class="btn-text">进行补签</span>
        </div>
        <div v-else
          class="btn-box flex disable">
          <span class="btn-text">进行补签</span>
        </div>
      </template>
    </template>
    <span class="btn-text-sub">打卡时间 {{punchinLimit}}</span>
  </div>
</template>

<script>
  import {clog } from "../../../../api/punchin";
  import {withinLimitTime, parseTime} from "../../../../utils/tools";
  import {punchinLimitTime} from "../../../../utils/config";
  const [[startHour, startMin],[endHour, endMin]] = punchinLimitTime
  export default {
    name: "ClockTip",
    props: {
      targetDate: {
        type: Object,
        default() {
          return {}
        }
      },
      source: {
        type: String,
        default: '首页'
      }
    },
    data() {
      return {
        disable: false,  // 是否超出打卡时间
        supplyDisable: false,  // 是否超出打卡时间
        isSupply: false,  // 是否可补签
        punchinLimit: `${startHour}:${startMin} - ${endHour}:${endMin}`,
      }
    },
    methods: {
      initBtnState() {              
        if(this.targetDate.today) { 
          // 可打卡
          this.disable = !withinLimitTime()
          // this.isSupply = false // 当天不允许补卡
          this.isSupply = +new Date() > new Date().setHours(endHour, endMin, 0, 0) && Number(this.$store.state.pinchinInfo.signCardNum) > 0// 当天允许补卡
          
        } else {
          this.isSupply = true
          this.supplyDisable = Number(this.$store.state.pinchinInfo.signCardNum) <= 0
        }
      },
      // 点击去打卡
      toPinchin() {
        if(this.disable) return
        this.punchinFn()
        try {
          clog("早安打卡", [
            "vjf-h5-log",
            "transTime",
            "openid",
            "好物好生活平",
            location.href.split("?")[0],
            "点击打卡",
            this.source + "点击打卡"
          ]);
          _hmt.push(["_trackEvent", "click", this.source, "点击进行打卡"]);
        } catch(e) {
          console.log('埋点失败' + e);        
        }
      },
      // 点击补签
      toSignPinchin() {
        if(!this.targetDate.nowDate) this.targetDate.nowDate = +new Date()
        const punchinDate = parseTime(this.targetDate.nowDate, '{yyyy}-{mm}-{dd}')
        this.punchinFn(punchinDate)
        
        try {
            clog("早安打卡", [
            "vjf-h5-log",
            "transTime",
            "openid",
            "好物好生活平",
            location.href.split("?")[0],
            "点击打卡",
            this.source + "进行补签"
          ]);
          _hmt.push(["_trackEvent", "click", this.source, "点击进行补签"]);
        } catch(e) {
          console.log('埋点失败' + e);        
        }
      },
      // 告诉父组件补签卡
      punchinFn(punchinDate='') {
        if(this.punchinFn.prevent) return
        setTimeout(() => {
          this.punchinFn.prevent = false
        },2000)
        this.punchinFn.prevent = true  
        this.$emit('click', punchinDate)
      }
    },
    watch: {
      'targetDate.nowDate': function() {
        this.initBtnState()
      }
    },
    created() {
      this.initBtnState()
    }
  }
</script>

<style scoped lang="scss">
  .daka-img-box {
    width: 628px;
    height: 640px;
    margin: 30px auto 0;
    border-radius: 10px;
    overflow: hidden;
  }
  .to-clock {
    background: url("../../imgs/bg-notip.jpg");
    background-size: 100% 100%;
    .to-tip {
      height: 38px;
      width: 368px;
      margin-top: 244px;
    }
    .btn-box {
      height: 70px;
      width: 288px;
      margin-top: 170px;
      background: #fff;
      border-radius: 8px;
      &.disable {
        background: #cecece;
      }
      .btn-text {
        font-size: 32px;
        text-align: center;
        color: #333;
      }
    }
    .btn-text-sub {
      font-size: 20px;
      color: #444;
      margin-top: 14px;
    }
  }
</style>
