<template>
  <div class="clock-box">
      <template v-if="oldSpecialDay">
      <div
        :style="{backgroundImage: `url(${imgBg}?v=1)`}"
        class="dom2img-box old-style  wrap-shadow"
        ref="clockImg"
      >
      <!-- 遮罩,图片为生成时阻止长按保存图片 -->
        <div class="shadow-longpress"></div>
        <img class="morning-img" v-if="morningImg" :src="morningImg" alt />
        <!-- 特殊日期-->
        <template v-if="specialDay">
          <div class="top-box">
            <div class="clock-info">
              <div class="img-box flex">
                <img :src="headImgBase64 || userInfo.headImg" alt />
              </div>
              <div class="clock-detail flex-column">
                <span>{{minutes}}</span>
                <span>{{`连续早起${pinchinInfo.continueNum}天`}}</span>
                <span>{{date}}</span>
              </div>
            </div>
          </div>
        </template>
        <template v-else>
          <div class="top-box">
            <img class="morning-text" src="../imgs/morning.png" />
            <div class="clock-info">
              <div class="img-box flex">
                <img :src="headImgBase64 || userInfo.headImg" alt />
              </div>
              <div class="clock-detail flex-column">
                <span>{{minutes}}</span>
                <span>{{`连续早起${pinchinInfo.continueNum}天`}}</span>
                <span>{{date}}</span>
              </div>
            </div>
          </div>
          <div class="bottom-box flex">
            <div class="bottom-left flex">
              <img src="../imgs/qr-code.jpg" alt />
              <span>扫码互道早安</span>
            </div>
            <div class="bottom-right flex">
              <span>{{pinchinInfo.punchinTips || '尚未佩妥剑，转眼便江湖。 愿历尽千帆，归来仍少年。'}}</span>
            </div>
          </div>
        </template>
      </div>
    </template>

    <template v-else>
      <div
        :style="{backgroundImage: `url(${imgBg}?v=1)`}"
        class="dom2img-box-new new-style  wrap-shadow"
        ref="clockImg"
      >
      <!-- 遮罩,图片为生成时阻止长按保存图片 -->
        <div class="shadow-longpress"></div>
        <img class="morning-img" v-if="morningImg" :src="morningImg" alt />
        <div class="content-box" :style="{backgroundImage: `url(${blackShadow}?v=1)`}">
          <div class="content-box-new">
            <!-- 左上角morning图标 -->
            <img class="icon-morning" src="../imgs/new-morning-long.png" alt />

            <!-- 中间节日文字,鸡汤文部分 -->
            <div class="middle-box">
              <img src="../imgs/new-line-long.png" alt class="line-vertical" />
              <div class="text-morning-box" v-if="!specialDay">
                <img src="../imgs/new-zaoan-long.png" alt class="icon-morning-text" />
                <div class="text-jitang">
                  <!-- 坎坷既是成功的铺路石，又是意志的试金石，更是能力的磨刀石 -->
                  {{pinchinInfo.punchinTips}}
                  <div class="half-block">
                    <div class="px-border-left"></div>
                    <div class="px-border-right"></div>
                    <div class="px-border-top"></div>
                    <div class="px-border-bottom"></div>
                  </div>
                </div>
              </div>
            </div>

            <div class="bottom-box">
              <div class="userInfo">
                <img class="headImg" :src="headImgBase64 || userInfo.headImg" alt />
                <span class="nickname">{{userInfo.nickname}}</span>
              </div>
              <div class="punchinInfo">
                <div class="left">
                  <span class="punchin-time">{{minutes}}</span>
                  <span class="punchin-date">{{date}}</span>
                </div>
                <div class="right">
                  <span class="desc">连续早起</span>
                  <span class="punchin-days">
                    {{pinchinInfo.continueNum}}
                    <span class="unit">天</span>
                  </span>
                </div>
              </div>

              <div class="qr-box">
                <div class="left">
                  <div class="desc">
                    <span>好物好生活</span>
                    <span>一起加入早安计划！</span>
                    
                  </div>
                  <div class="btn-red">
                    <span>长按识别二维码</span>
                    <img src="../imgs/arrow-btn.png" alt />
                  </div>
                </div>

                <div class="right">
                  <img src="../imgs/new-qr-long.png" alt />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </template>
    <div v-if="morningImg" class="text-tip">长按图片保存到手机</div>
  </div>
</template>

<script>
import html2canvas from "html2canvas";

import { mapState } from "vuex";
import { getUrlParam, parseTime } from "../../../utils/tools";
import { baseUrl } from "../../../utils/request";
export default {
  name: "morning-clock",
  props: {
    pinchinInfo: {
      type: Object,
      default() {
        return {};
      }
    },
    headImgBase64: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      morningImg: "",
      morningImgURL: baseUrl.morningImgURL,
      defaultImg: require("../imgs/bg-morning.jpg"),
      blackShadow: require('../imgs/black-shadow.png')
      // blackShadow: ''
    };
  },
  computed: {
    ...mapState(["userInfo"]),
    minutes() {
      const hourM = this.pinchinInfo.punchinTime.split(" ")[1];
      const flag = hourM.indexOf(":", 3);
      return hourM.substring(0, flag);
    },
    date() {
      return parseTime(
        new Date(this.pinchinInfo.punchinTime.split(" ")[0]),
        "{yyyy}年{mm}月{dd}日"
      );
    },
    imgBg() {
      const punchinBgImg = this.pinchinInfo.punchinBgImg;
      return punchinBgImg ? this.morningImgURL + punchinBgImg : this.defaultImg;
    },
    specialDay() {
      return this.pinchinInfo.punchinBgImg
        ? this.pinchinInfo.punchinBgImg.length > 10
        : false;
    },
    oldSpecialDay() {
      const bgImg = this.pinchinInfo.punchinBgImg;
      const isOld = (bgImg.indexOf("2019") === 0 || bgImg.indexOf("20200101") === 0 || bgImg.indexOf("20200102") === 0)
      if (this.specialDay && isOld) {
        return true;
      } else {
        return false;
      }
    },
    punchinTips() {
      return this.pinchinInfo.punchinTips.split("").filter(item => item);
    }
  },
  mounted() {
    this.timer = setTimeout(() => {
      const dom = this.$refs.clockImg;
      const domStyle = getComputedStyle(dom)
      html2canvas(dom, {
        useCORS: true,
        backgroundColor: "rgba(255,255,255,0)",
        scale: 3,
        width: parseInt(domStyle.width),//设置canvas尺寸与所截图尺寸相同，防止白边
        height: parseInt(domStyle.height),//防止白边
      }).then(canvas => {
        this.morningImg = canvas.toDataURL("image/jpg");
      });
      // domtoimage.toPng(dom,{useCORS: true })
      //   .then(dataUrl => {
      //     this.morningImg = dataUrl;
      //   })
      //   .catch(function (error) {
      //     console.error('oops, something went wrong!', error);
      //   });
    }, 100);
  },
  destroyed() {
    clearTimeout(this.timer);
  }
};
</script>

<style scoped lang="scss">
.clock-box {
  width: 628px;
  position: relative;
  min-height: 640px;
  .morning-img {
    width: 100%;
    height: 100%;
    border-radius: 8px;
    background-color: #fff;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 59;
  }
  .shadow-longpress {
    width: 100%;
    height: 100%;
    border-radius: 8px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 58;
  }
  .box-radius8 {
    border-radius: 8px;
    // height: auto;
    // overflow: hidden;
  }
  .old-style {
    &.dom2img-box {
      width: 100%;
      height: 640px;
      background: url("../imgs/bg-morning.jpg") no-repeat;
      background-size: 100% 100%;
      position: relative;
      color: #fff;
      border-radius: 8px;
      position: relative;
    }
    .top-box {
      height: 500px;
      .morning-text {
        width: 70px;
        height: 136px;
        position: absolute;
        top: 28px;
        left: 18px;
        font-size: 66px;
      }
      .clock-info {
        height: 94px;
        width: 242px;
        float: right;
        margin-top: 30px;
        display: flex;
        justify-content: space-between;
        .img-box {
          height: 100%;
          width: 92px;
          background-color: rgba(150, 150, 150, 0.45);
          img {
            height: 76px;
            width: 76px;
          }
        }
        .clock-detail {
          height: 100%;
          width: 140px;
          background-color: rgba(150, 150, 150, 0.45);
          align-items: flex-start;
          span {
            height: 24px;
            text-indent: 8px;
            font-size: 20px;
            text-size-adjust: none;
            position: relative;
            top: 3px;
            &:nth-of-type(1) {
              height: 40px;
              font-size: 32px;
            }
            &:nth-of-type(3) {
              height: 22px;
              font-size: 18px;
            }
          }
        }
      }
    }

    .bottom-box {
      height: 140px;
      width: 100%;
      background: rgba(150, 150, 150, 0.45);
      .bottom-left {
        height: 120px;
        width: 186px;
        position: relative;
        &:after {
          content: "";
          position: absolute;
          top: 0;
          right: 0;
          height: 100%;
          width: 2px;
          background: #fff;
        }
        img {
          height: 120px;
          width: 120px;
        }
        span {
          width: 26px;
          line-height: 21px;
          font-size: 20px;
          text-align: right;
          color: #fff;
        }
      }
      .bottom-right {
        flex: 1;
        height: 100%;
        span {
          width: 410px;
          line-height: 48px;
          text-align: left;
          font-size: 28px;
          font-weight: bold;
          color: #fff;
        }
      }
    }
  }
  .new-style {
    &.dom2img-box-new {
      width: 628px;
      height: 1115px;
      background: url("../imgs/new-bg.jpg") no-repeat;
      background-size: 628px 1118px;
      position: relative;
      color: #fff;
      border-radius: 8px;
      position: relative;
      overflow: hidden;
      border: none;
    }
    .content-box {
      width: 100%;
      height: 100%;
      // display: flex;
      justify-content: center;
      align-items: center;
      background-size: 100%;
    }
    .content-box-new {
      width: 750px;
      height: 1332px;
      position: relative;
      background-color: rgba(255, 255, 255, 0);
      overflow: hidden;
      // display: none;
      transform: scale(0.83) translateX(-75px) translateY(-135px);
      // border: 1px solid #000;
      .icon-morning {
        height: 219px;
        width: 73px;
        position: absolute;
        top: 42px;
        right: 37px;
      }

      .middle-box {
        height: 380px;
        width: 100%;
        margin-top: 192px;
        display: flex;
        .line-vertical {
          height: 100%;
          width: 12px;
          margin-left: 42px;
        }
        .text-morning-box {
          height: 100%;
          width: 626px;
          margin-left: 35px;
          .icon-morning-text {
            height: 193px;
            width: 242px;
          }
          .text-jitang {
            max-width: 560px;
            line-height: 58px;
            font-family: "宋体";
            // font-weight: bold;
            font-style: oblique;
            font-size: 36px;
            text-shadow:2px 2px 2px rgba(0,0,0,0.2);
            letter-spacing: 3px;
            margin-top: 51px;
            position: relative;
            .half-block {
              height: 100px;
              width: 562px;
              position: absolute;
              top: 25px;
              left: 16px;
              // background-image: url("../imgs/new-block-long.png");
              background-repeat: no-repeat;
              background-size: 547px 99px;
              background-position: bottom left;
              border: none;

              .px-border-left {
                position: absolute;
                height: 12px;
                width: 1px;
                bottom: 0;
                left: 0;
                background: #fff;
              }
              .px-border-right {
                position: absolute;
                height: 100%;
                width: 1px;
                bottom: 0;
                right: 0;
                background: #fff;
              }
              .px-border-top {
                position: absolute;
                height: 1px;
                width: 14px;;
                top: 0;
                right: 0;
                background: #fff;
              }
              .px-border-bottom {
                position: absolute;
                height: 1px;
                width: 100%;
                bottom: 0;
                left: 0;
                background: #fff;
              }
            }
          }
        }
      }

      .bottom-box {
        width: 100%;
        height: 435px;
        margin-top: 286px;
        font-family: 微软雅黑;
        .userInfo {
          width: 100%;
          height: 50px;
          display: flex;
          align-items: center;
          .headImg {
            height: 50px;
            width: 50px;
            border-radius: 50%;
            margin-left: 56px;
          }
          .nickname {
            font-size: 28px;
            margin-left: 22px;
          }
        }

        .punchinInfo {
          height: 108px;
          display: flex;
          margin-top: 20px;
          .left {
            width: 470px;
            margin-left: 58px;
            box-sizing: border-box;
            padding-left: 22px;
            display: flex;
            flex-direction: column;
            position: relative;
            &:before {
              content: "";
              height: 85px;
              width: 3px;
              background-color: #fff;
              position: absolute;
              top: 12px;
              left: 0;
            }
            .punchin-time {
              font-size: 62px;
              position: relative;
              top: -7px;
              font-style: oblique;
              font-weight: bold;
            }
            .punchin-date {
              font-size: 26px;
              letter-spacing: 1px;
            }
          }
          .right {
            width: 186px;
            box-sizing: border-box;
            padding-left: 22px;
            display: flex;
            flex-direction: column;
            position: relative;
            &:before {
              content: "";
              height: 85px;
              width: 3px;
              background-color: #fff;
              position: absolute;
              top: 12px;
              left: 0;
            }
            .desc {
              font-size: 26px;
            }
            .punchin-days {
              position: relative;
              top: 6px;
              font-size: 62px;
              font-style: oblique;
              font-weight: bold;
              .unit {
                font-size: 26px;
              }
            }
          }
        }

        .qr-box {
          width: 672px;
          height: 220px;
          border-radius: 26px;
          background-color: rgb(255, 255, 255);
          margin: 36px auto;
          font-family: 黑体;
          display: flex;
          .left {
            height: 100%;
            width: 484px;
            box-sizing: border-box;
            padding: 26px 0 0 40px;
            color: #333;
            font-size: 28px;
            .desc {
              display: flex;
              flex-direction: column;
              span {
                height: 42px;
                display: flex;
                align-items: center;
              }
            }
            .btn-red {
              height: 52px;
              width: 245px;
              color: #fff;
              font-size: 24px;
              border-radius: 26px;
              background-color: #eb0626;
              margin-top: 16px;
              display: flex;
              justify-content: center;
              align-items: center;
              span {
                position: relative;
                top: 2px;
              }
              img {
                height: 32px;
                width: 32px;
                margin-left: 12px;
              }
            }
          }
          .right {
            img {
              height: 140px;
              width: 140px;
              margin: 38px 0 0 4px;
            }
          }
        }
      }
    }
  }
  .text-tip {
    width: 100%;
    height: 44px;
    line-height: 44px;
    font-size: 20px;
    text-align: center;
    color: #646464;
    position: absolute;
    bottom: -42px;
  }
}
</style>
