import {myPunchin, punchinToday, punchinSign} from "../../../api/punchin";
import {mapState, mapGetters, mapMutations} from 'vuex';
import { Toast, Indicator } from 'mint-ui';
import {getClientHeight, htmlFontSize} from "../../../utils/tools";
export default {
  data() {
    return {
      choosedDate: {}
    }
  },
  computed: {
    ...mapState([
      'userInfo',
      'pinchinInfo'
    ]),
    ...mapGetters([
      'hasPunchin'
    ])
  },
  methods: {
    ...mapMutations([
      'setPinchinInfo'
    ]),
    // 处理短屏显示
    handleShortScreen(num=1200) {
      try {
        const fontSize = htmlFontSize()
        const scaleHeight = 75/fontSize*getClientHeight()
        console.log('scaleHeight', scaleHeight)
        if(scaleHeight <= num) this.shortScreen = true
        return this.shortScreen
      } catch (e) {
        console.log('handleShortScreen', e)
      }
    },
    // 补卡或者打卡后更新状态
    getPunchinStatus() {
      return myPunchin().then(res => {
        const {reply, result} = res
        if ((+result.businessCode === 0 || +result.businessCode === 1) && reply) {
          reply.hasRequest = true
          reply.punchinTime = reply.lastPunchinTime
          reply.replyTime = res.replyTime
          this.setPinchinInfo(reply)
        }

        this.choosedDate = {
          clocked: this.hasPunchin,
          clockData: this.pinchinInfo,
          nowDate: +new Date(),
          today: true
        }
      })
    },
    init() {
      const {hasRequest} = this.pinchinInfo
      this.initPromise = new Promise((resolve, reject) => {
        if(!hasRequest) {
          this.getPunchinStatus().then(res => {
            // console.log('this.hasPunchin',this.hasPunchin);
            resolve()
          })
        } else {
          this.choosedDate = {
            clocked: this.hasPunchin,
            clockData: this.pinchinInfo,
            today: true
          }
          resolve()
        }
      })
    },
    // 去打卡 或者补签
    toPinchin(punchinDate) {
      Indicator.open(punchinDate ? '补卡中' : '打卡中')
      // 判断是打卡还是补卡
      const punchinFn = punchinDate ? punchinSign : punchinToday
      const data = punchinDate ? {punchinDate} : {}
      punchinFn(data)
        .then(res => {
          Indicator.close()
          if(+res.result.businessCode === 0) {
            this.isShowDakaCtr = false
            Toast({
              message: punchinDate ? '补卡成功' : '打卡成功',
              position: 'bottom',
              duration: 1500
            });
            if (punchinDate) { // 补卡成功
              this.setPinchinInfo({
                signCardNum: this.pinchinInfo.signCardNum - 1
              })
              this.getPunchinStatus()
                .then(() => {
                  this.initMonth()
                })
              
            } else { // 打卡成功
              this.getPunchinStatus()
            }
          } else {
            Toast({
              message: res.result.msg,
              position: 'bottom',
              duration: 1500
            });
          }
        })
    },
  },
  created() {
    this.init()
    this.handleShortScreen()

    // document.getElementById("wrap").scrollTop = 0
  }
}
